import { set, useForm } from "react-hook-form";
import { useRef, useState } from "react";
import { yupResolver } from "@hookform/resolvers/yup";
import * as yup from "yup";
import styles from "./CRUD.module.css"
import { Link } from "react-router-dom";
import axios from "axios";
const schema = yup.object().shape({
    name: yup
        .string()
        .required("Vui lng nhp tn nhn vin!")
        .max(50, "Tn ti a 50 k t!"),
    phone: yup
        .string()
        .required("Vui lng nhp s in thoi!"),
    address: yup
        .string()
        .required("Vui lng nhp a ch!"),
    email: yup
        .string()
        .required("Vui lng nhp email!"),
    password: yup
        .string()
        .required("Vui lng nhp mt khu!")
        .min(7, "Mt khu phi c nhiu hn 6 k t")
});
export default function AddStaff (props) {
    // console.log(props);
    const {
        register,
        reset,
        setFocus,
        handleSubmit,
        formState: {errors}
    } = useForm({resolver: yupResolver(schema)});
    const inputRef = useRef();
    const [msg, setMsg] = useState('');
    const [err, setErr] = useState('');
    const onSubmit = (data) => {
        // console.log(data);
        setMsg('');
        setErr('');
        axios({
            method: 'POST',
            url: 'http://localhost:5000/staff/add',
            data: {
                phone: data.phone,
                password: data.password, 
                name: data.name,
                email: data.email,
                address: data.address
            }
        })
        .then(res => {   
            try {
                console.log(res.data);
                if(res.data.id === 1) {//Tr v 200 v thnh cng
                    reset({
                        name: "",
                        phone: "",
                        address: "",
                        email: "",
                        password: "",
                    });
                    setMsg(res.data.message);
                    props.setChange(!props.isAdded);
                }
                else if (res.data.id === 2) {
                    setErr(res.data.message);
                    console.log(res.data.message);
                }
            }
            catch {
                console.log("Li response")
            }
            console.log(res.data.id);
        })
        .catch(err => {
            console.log(err);
        })
        setFocus("name");

        // console.log('Thm thnh cng');
        
    };
    return (
        <div className={styles.AddStaff}>
            <div className={styles.form} >
                <Link to="/admin/CRUD" className={styles.form_exit}>&times;</Link>
                <h1 className={styles.form_heading}>in cc thng tin cn thit  thm nhn vin</h1>
                {msg && (<h3 className={styles.form_message}>{msg}</h3>)}
                {err && (<h3 className={styles.form_error}>{err}</h3>)}
                <div className={styles.field}>
                    <label className={styles.form_label}>Tn nhn vin: </label>
                    <input ref={inputRef} className={styles.form_input}
                           placeholder="VD: Nguyn Vn Bin" {...register("name")} />
                    {/* Nu c li th hin th n ra cho ngi dng */}
                    {errors.name &&
                        <p className={styles.form_error}>{errors.name?.message}</p>}
                </div>
                <div className={styles.field}>
                    <label className={styles.form_label}>S in thoi: </label>
                    <input className={styles.form_input} {...register("phone")} />
                    {/* Nu c li th hin th n ra cho ngi dng */}
                    {errors.phone &&
                        <p className={styles.form_error}>{errors.phone?.message}</p>}
                </div>
                <div className={styles.field}>
                    <label className={styles.form_label}>a ch: </label>
                    <input className={styles.form_input} {...register("address")} />
                    {/* Nu c li th hin th n ra cho ngi dng */}
                    {errors.address &&
                        <p className={styles.form_error}>{errors.address?.message}</p>}
                </div>
                <div className={styles.field}>
                    <label className={styles.form_label}>Email: </label>
                    <input className={styles.form_input} {...register("email")} />
                    {/* Nu c li th hin th n ra cho ngi dng */}
                    {errors.email &&
                        <p className={styles.form_error}>{errors.email?.message}</p>}
                </div>
                <div className={styles.field}>
                    <label className={styles.form_label}>Mt khu: </label>
                    <input className={styles.form_input} placeholder="" {...register("password")} />
                    {/* Nu c li th hin th n ra cho ngi dng */}
                    {errors.password &&
                        <p className={styles.form_error}>{errors.password?.message}</p>}
                </div>

                <div className={styles.field_submit}>
                    <button className={styles.form_submit} onClick={handleSubmit(onSubmit)} type="submit">Submit</button>
                </div>
            </div>
        </div>
    );
}
