import {useForm} from "react-hook-form";
import {yupResolver} from "@hookform/resolvers/yup";
import * as yup from "yup";
import styles from './CreateEvent.module.css'

const schema = yup.object().shape({
    name: yup
        .string()
        .required("Vui lng nhp tn ca bn!")
        .max(50, "Tn ti a 50 k t!"),
    phone: yup
        .string()
        .required("Vui lng nhp mt khu!"),
    number: yup
        .string()
        .required("Vui lng nhp s lng ngi tham gia!"),
    time: yup
        .string()
        .required("Vui lng nhp thi gian din ra s kin!")
});

function CreateEvent() {
    const {
        register,
        reset,
        setFocus,
        handleSubmit,
        formState: {errors}
    } = useForm({resolver: yupResolver(schema)});
    var eventsAPI = 'http://localhost:5000/shopevent/add'

    function CreateEvent(data) {
        fetch(eventsAPI, {
            method: 'POST',
            headers: {
                'Content-Type': 'application/json'
            },
            body: JSON.stringify(data),
        })
    }

    const onSubmit = (data) => {
        CreateEvent(data);
        reset({
            name: "",
            phone: "",
            time: "",
            number: ""
        });
        setFocus("name");
    };
    return (
        <div className={styles.main}>
            <form className={styles.form} onSubmit={handleSubmit(onSubmit)}>
                <h1 className={styles.form_heading}>in cc thng tin cn thit  to s kin</h1>
                <div className={styles.field}>
                    <label className={styles.form_label}>Tn khch hng: </label>
                    <input className={styles.form_input} placeholder="VD: Nguyn Vn Bin" {...register("name")} />
                    {/* Nu c li th hin th n ra cho ngi dng */}
                    {errors.name &&
                        <p className={styles.form_error}>{errors.name?.message}</p>}
                </div>
                <div className={styles.field}>
                    <label className={styles.form_label}>S in thoi: </label>
                    <input className={styles.form_input} placeholder="VD: 0123456789" {...register("phone")} />
                    {/* Nu c li th hin th n ra cho ngi dng */}
                    {errors.phone &&
                        <p className={styles.form_error}>{errors.phone?.message}</p>}
                </div>
                <div className={styles.field}>
                    <label className={styles.form_label}>S lng ngi tham gia: </label>
                    <input className={styles.form_input} placeholder="VD: 5" {...register("number")} />
                    {/* Nu c li th hin th n ra cho ngi dng */}
                    {errors.number &&
                        <p className={styles.form_error}>{errors.number?.message}</p>}
                </div>
                <div className={styles.field}>
                    <label className={styles.form_label}>Thi gian: </label>
                    <input className={styles.form_input}
                           placeholder="VD: 17h-19h Th 6 ngy 1/7" {...register("time")} />
                    {/* Nu c li th hin th n ra cho ngi dng */}
                    {errors.time &&
                        <p className={styles.form_error}>{errors.time?.message}</p>}
                </div>
                <div className={styles.field_submit}>
                    <button className={styles.form_submit} type="submit">Submit</button>
                </div>
            </form>
        </div>
    );
}

export default CreateEvent