import { useForm } from "react-hook-form";
import { yupResolver } from "@hookform/resolvers/yup";
import * as yup from "yup";
import styles from "./Products.module.css"

const schema = yup.object().shape({
    name: yup
        .string()
        .required("Vui lng nhp tn sn phm!")
        .max(50, "Tn ti a 50 k t!"),
    price: yup
        .string()
        .required("Vui lng nhp gi sn phm!"),
    stock: yup
        .string(),
    type: yup
        .string()
        .required("Vui lng nhp phn loi sn phm!"),
    image: yup
        .string(),
    description: yup
        .string(),

});
const AddProduct = ({add}) => {
    const {
        register,
        reset,
        setFocus,
        handleSubmit,
        formState: { errors }
    } = useForm({ resolver: yupResolver(schema) });

    const onSubmit = (data) => {
        add(data);
        reset({
            name: "",
            price: "",
            stock: "",
            type: "",
            image: "",
            description: "",
        });
        setFocus("name");
    };
    return (
        <div className={styles.addProduct}>
            <form className={styles.form} onSubmit={handleSubmit(onSubmit)}>
                <h1 className={styles.form_heading}>in cc thng tin cn thit  thm sn phm</h1>

                <div className={styles.field}>
                    <label className={styles.form_label}>Tn sn phm: </label>
                    <input className={styles.form_input}
                        placeholder="VD: Bc su" {...register("name")} />
                    {/* Nu c li th hin th n ra cho ngi dng */}
                    {errors.name &&
                        <p className={styles.form_error}>{errors.name?.message}</p>}
                </div>
                <div className={styles.field}>
                    <label className={styles.form_label}>Gi tin: </label>
                    <input className={styles.form_input}
                        placeholder="VD: 50000" {...register("price")} />
                    {/* Nu c li th hin th n ra cho ngi dng */}
                    {errors.phone &&
                        <p className={styles.form_error}>{errors.phone?.message}</p>}
                </div>
                <div className={styles.field}>
                    <label className={styles.form_label}>Phn loi: </label>
                    <input className={styles.form_input} placeholder="VD:  ung in 0/ Sch in 1" {...register("type")} />
                </div>
                <div className={styles.field}>
                    <label className={styles.form_label}>S lng cn li: </label>
                    <input className={styles.form_input} placeholder="VD: 10" {...register("stock")} />
                </div>
                <div className={styles.field}>
                    <label className={styles.form_label}>Link nh m t sn phm: </label>
                    <input className={styles.form_input} placeholder="VD: http://image.jpg" {...register("image")} />
                </div>
                <div className={styles.field}>
                    <label className={styles.form_label}>M t sn phm: </label>
                    <input className={styles.form_input} placeholder="Bc su mang n v ngt,..." {...register("description")} />
                </div>
                <div className={styles.field_submit}>
                    <button className={styles.form_submit} type="submit">Submit</button>
                </div>
            </form>
        </div>
    );
}
export default AddProduct